	computed: {
		proDetail() {
			return {
				price: this.getRndInteger(150,300),
				factory: this.getRndInteger(200,500),
				coverNum: this.getRndInteger(500,2000),
				browseNum: this.getRndInteger(500,5000)
			}
		}
	},
	methods: {
		/* 生成随机id数 */
		getRndInteger(min, max) {
			return Math.floor(Math.random() * (max - min + 1)) + min;
		},
		bannerChange: function(e) {
			this.bannerIndex = e.detail.current
		},
		previewImage: function(e) {
			let index = e.currentTarget.dataset.index;
			uni.previewImage({
				current: this.banner[index],
				urls: this.banner
			})
		},
		/* 分享给好友 */
		btnShare() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 0,
			    href: "http://uniapp.dcloud.io/",
			    title: "uni-app分享",
			    summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
			    imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
			    success: function (res) {
			        console.log("success:" + JSON.stringify(res));
			    },
			    fail: function (err) {
			        console.log("fail:" + JSON.stringify(err));
			    }
			});
		},
		/* 拨打 */
		callUp() {
			uni.makePhoneCall({
			    phoneNumber: this.businPhone
			});
		},
		/* 导航 */
		navigation() {
			const latitude = 30.668568;
			const longitude = 104.112282;
			uni.openLocation({
				name: "成都成华SM广场",
				address: "四川省成都市成华区二环路东二段29号",
				latitude,
				longitude,
				scale: 18
			})
		},
		/* 为您推荐中商品详情跳转 */
		gotoDetail(url,val) {
			if (this.type != 1 && val == 2) return
			uni.navigateTo({
				url
			})
		},
		/* 底部操作栏跳转 */
		gotoPage(url) {
			uni.switchTab({
				url
			})
		},
		/* 底部share分享改变事件 */
		change(e) {
			console.log('是否打开:' + e.show)
			if (!e.show) {
				this.showshare = false
			}
		},
		togglePopup(type, open) {
			this['show' + open] = true
		},
		cancel(type) {
			this['show' + type] = false
		},
	},
	/* 小程序中右上角分享 */
	onShareAppMessage(res) {
		if (res.from === 'button') {
			console.log(res)
		}
		return {
		  title: '自定义分享标题',
